/* ==========================================================================
      Import Web Fonts
      ========================================================================== */
/* Condensed Regular */
@font-face {
  font-family: "League Gothic";
  src: url("fonts/League-Gothic/LeagueGothic-Regular.otf");
  font-weight: 600;
  font-style: normal;
}

/* Regular */
@font-face {
  font-family: "Courier";
  src: url("fonts/Courier Regular/Courier Regular.ttf");
  font-weight: 600;
  font-style: normal;
}

/* ==========================================================================
      Grid Container
      ========================================================================== */
.form-container {
  display: flex;
  justify-content: space-between;
  margin: 0 10px;
  margin-top: 15px;
  margin-left: 100px;
  margin-right: -150px;
  padding: 20px 20px;
  overflow: hidden;
  border-collapse: collapse;
  width: 100%;
  height: 100%;
  background-color: #e0e0e0;
  border-radius: 0px;
  font-size: 12px;
  border-radius: 0px;
  border: 6px solid #cfcfcf;
}

.line {
  margin-top: 20px;
  background-color: #a3a3a3;
  height: 1px;
  width: 118.8%;
}

.flex {
  margin-top: 25px;
  width: 30%;
  flex: 1;
  height: 800px;
  overflow-y: scroll;
  overflow-x: scroll;
  padding-right: 50px; /* Increase/decrease this value for cross-browser compatibility */
  box-sizing: content-box; /* So the width will be 100% + 17px */
  margin-right: 30px;
  -ms-overflow-style: none;
}

.flex::-webkit-scrollbar {
  /* WebKit */
  width: 10px;
  height: 0;
}
.flex::-webkit-scrollbar-thumb {
  background: #212121;
}

.flex2 {
  width: 70%;
  flex: 1;
  height: 800px;
  overflow-y: hidden;
  overflow-x: hidden;
  padding-right: 30px; /* Increase/decrease this value for cross-browser compatibility */
  box-sizing: content-box; /* So the width will be 100% + 17px */
  margin-right: 30px;
}
@media (max-width: 767px) {
  .form-container {
    flex-direction: column;
  }
  .flex,
  .flex2 {
    width: auto;
  }
}

/* ==========================================================================
      General Header Styles
      ========================================================================== */
.slurm-title {
  padding-left: 120px;
  margin-top: 10px;
  margin-left: -118px;
 
}
.slurm-title p {
  font-size: 34px;
  font-family: "League Gothic";
}


/* ==========================================================================
      Script
      ========================================================================== */

.script-title {
  padding-left: 10px;
  margin-top: 0px;
  margin-left: 100px;
}
.script-title p {
  font-size: 70px;
  font-weight: 600;
  font-family: "League Gothic";
  color: #c8102e;
}
.script-titleGen {
  padding-left: 20px;
  margin-top: -30px;
  margin-left: 90px;
  margin-bottom: -30px;
}
.script-titleGen p {
  font-size: 40px;
  font-weight: 600;
  font-family: "League Gothic";
  color: #d27373;
}

.script-code {
  height: 450px;
  background: #fff2b7;
  border-radius: 0px;
  border: 10px solid #000000;
  display: flex;
  flex-direction: column;
  padding: 10px;
  overflow: auto;
  font-family: "Courier";
  font-weight: 600;
  font-size: 15px;
}

.code {
  margin-bottom: 30px;
  display: flex;
  flex-direction: column;
}
.space {
  width: 20px;
}

p {
  padding: 0;
  margin: 0;
}

.email-check {
  display: flex;
  flex-direction: row;
}

.email-check .form-check:not(:first-child) {
  margin-left: 10px;
}

.form-label {
  font-weight: 700;
}
.highlight {
  background: rgb(240, 226, 166);
  font-weight: 500;
  padding: 0px 4px;
  color: #d27373;
  border-radius: 2px;
}

.text-muted {
  line-height: 20px;
  font-size: 12px;
}

/* ==========================================================================
      Copy Button
      ========================================================================== */
.btn {
  padding: 14px 24px;
  border: 0 none;
  font-weight: 700;
  letter-spacing: 1px;
  text-transform: uppercase;
  border-radius: 0%;
  margin-left: -12px;
  margin-top: -5px;
  width: auto;
}

.btn:focus,
.btn:active:focus,
.btn.active:focus {
  outline: 0 none;
}

.btn-primary {
  background: #000000;
  color: #ffffff;
}

.btn-primary:hover,
.btn-primary:focus,
.btn-primary:active,
.btn-primary.active,
.open > .dropdown-toggle.btn-primary {
  background: #d27373;
}

.btn-primary:active,
.btn-primary.active {
  background: #d27373;
  box-shadow: none;
}

.btnLink {
font-family: default;
font-size: 12px;
text-transform: lowercase; 
padding: 0!important;
  border: none;
  font-weight: 700;
  letter-spacing: 1px;
  border-radius: 0%;
  width: auto;
  margin-left : 1px;
  margin-top : 0.1px;
  background: none!important;
  outline: none;
  color: #c8102e;
  font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  text-decoration: underline;
  cursor: pointer;
  box-shadow: none;
}
.btnLink:active {
  outline: none !important;
  outline-offset: none !important;
  box-shadow: none;
  }
.btnLink:focus {
outline: none !important;
outline-offset: none !important;
box-shadow: none;
}


  
  




/* ==========================================================================
      Submit Script Guide
      ========================================================================== */
.submit-script {
  padding-top: 10px;
  padding-left: 30px;
}

.submit-script div {
  padding: 5px;
  padding-right: 100px;
  border-radius: 4px;
  display: inline-block;
  background: #f1f1f1;
}
.row-container {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
}
.row-container:not(:first-child) {
  margin-left: 5px;
}
.row-container label {
  margin-left: 10px;
}

div.content {
  width: auto;
  margin-right: 200px;
}

.borderColor {
  border-color: #c8102e !important;
}
.borderRemove {
  border-color: none;
}

.borderColorCluster {
  border: 1px solid #c8102e !important;
}

.borderColorModules {
  border: 1px solid #c8102e !important;
}

.borderColorExecutable {
  border: 1px solid #c8102e !important;
}

